.saved-news {
  background-color: #fff;
}

.saved-news__text_group {
  display: flex;
  flex-direction: column;
  padding: 40px 80px 0px 106px;
  max-width: 712px;
  max-height: 198px;
}

.saved-news__title {
  max-width: 712px;
  height: 24px;
  margin: 0;
  font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
}

.saved-news__subtext {
  max-width: 530px;
  height: 92px;
  margin: 28px 0 30px;
  font-family: Roboto-Slab, Inter, Arial, Helvetica, sans-serif;
  font-size: 40px;
  font-weight: 400;
  line-height: 46px;
  text-align: left;
}

.saved-news__search-grouping {
  max-width: 712px;
  height: 24px;
  margin: 0 0 56px;
}

.saved-news__search-text {
  font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
}

.saved-news__search-text_keywords-primary {
  font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
}

.saved-news__search-text_keywords-secondary {
  font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .saved-news {
    max-width: 1279px;
  }

  .saved-news__text_group {
    padding: 24px 40px 0 32px;
  }

  .saved-news__title {
    max-width: 712px;
    max-height: 24px;
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
  }

  .saved-news__subtext {
    max-width: 336px;
    max-height: 68px;
    margin: 16px 0 16px;
    font-family: Roboto-Slab, Inter, Arial, Helvetica, sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 34px;
    text-align: left;
  }

  .saved-news__search-grouping {
    max-width: 288px;
    max-height: 48px;
  }

  .saved-news__search-text {
    max-width: 712px;
    max-height: 24px;
    margin: 0 0 32px;
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
  }

  .saved-news__search-text_keywords-primary {
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
  }

  .saved-news__search-text_keywords-secondary {
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .saved-news {
    max-width: 767px;
  }

  .saved-news__text_group {
    padding: 8px 16px 32px;
  }

  .saved-news__title {
    max-width: 288px;
    max-height: 24px;
    margin: 0 auto;
    color: #1a1b2280;
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
  }

  .saved-news__subtext {
    max-width: 288px;
    max-height: 68px;

    margin: 16px auto 50px;
    font-family: Roboto-Slab, Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 34px;
    text-align: left;
  }

  .saved-news__search-grouping {
    max-width: 288px;
    max-height: 48px;
    margin: 0 auto;
  }

  .saved-news__search-text {
    max-width: 288px;
    max-height: 48px;
    margin: 0 0 32px;
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
  }

  .saved-news__search-text_keywords-primary {
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
  }

  .saved-news__search-text_keywords-secondary {
    font-family: Roboto, Inter, Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-align: left;
  }
}
